import React, {useState} from 'react'
import Axios from 'axios'
import Recipe from './components/Recipe'
import {v4 as uuidv4} from 'uuid'
import styled from 'styled-components'
import Alert from './components/Alert'
import back from './bg1.jpg'

const StyledApp = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
width: 100%;
font-family: 'Yantramanav', sans-serif;
font-weight: 600;
color: white;
`

const AppHeader = styled.div`
background: url(${back}) no-repeat center center fixed; 
background-size: cover;
min-width: 100%;
min-height: 100vh;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;  

@media (max-width: 700px) {
  min-height: 100vh;
}
`

const AppTitle = styled.div`
width: 100%;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;


h1 {
  font-size: 50px;
  margin: 0px;
  font-family: 'Righteous', cursive;
}

p {
  margin: 0px;
  font-size: 20px;
  padding-top: 10px;
}

`
const AppSearch = styled.div`
padding-top: 30px;
padding-bottom: 30px;
width: 100%;
display: flex;
align-items: center;
justify-content: center;


input[type=text] {
  padding: 10px;
  width: 300px;
  border: none;
  outline: none;
}

input[type=submit] {
  padding: 10px;
  border: none;
  &:hover {
    color: orange;
    cursor: pointer;
  }
}

@media (max-width: 700px) {
  input[type=text] {
    width: 240px;
  }
}
  
`

const AppContent = styled.div`
display: flex;
flex-wrap: wrap;
width: 80%;
justify-content: center;
background-color: rgba(0,0,2,255);
min-width: 100%;
min-height: 100vh;
padding-top: 20px;

`

const ReturnButton = styled.div`
position: absolute;
right: 20px;
top: 20px;
background-color: #ffd102;
height: 50px;
width: 50px;
display: flex;
align-items: center;
justify-content: center;
font-size: 30px;
cursor: pointer;
z-index: 3;
border-radius: 30px;

&:hover {
  background-color: #3d87a7;
}
`

const App = () => {
  const [query, setQuery] = useState("")
  const [recipes, setRecipes] = useState([])
  const [alert, setAlert] = useState("")
  const [openRecipes, setOpenRecipes] = useState(false)

  const APP_ID = "99ced3a4";
  const APP_KEY = "0744a4777d691ee9ce96ccc79024e306"

  const url =`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`

  const getData = async () => {
    if (query!=="") {
      const result = await Axios.get(url)
      if (!result.data.more) {
        return (
          setAlert("No food with such name")
        )
      }
      setRecipes(result.data.hits)
      console.log(result)
      setQuery("")
      setAlert("")
      setOpenRecipes(true)
    } else {
      setAlert("Please fill the form")
      setQuery("")
    }
    
  }

  const onChange = e => {
    setQuery(e.target.value)
  }

  const onSubmit = e => {
    e.preventDefault();
    getData();
  }
  return (
    <StyledApp>
      
      {!openRecipes && 
      <AppHeader>
        <AppTitle>
          <h1>Hungry?</h1>
          <p>Find ideas for a tasty meal!</p>
        </AppTitle>
        <AppSearch>
          <form onSubmit={onSubmit}>
            <input 
            type="text" 
            placeholder="Search recipe" 
            autoComplete="off" 
            onChange={onChange}
            value={query} />
            <input 
            type="submit" 
            value="Search"/>
            
          </form>
        </AppSearch>
      </AppHeader>}
      {alert !== "" && <Alert alert={alert}/>}
      {openRecipes && 
      <AppContent id="content">
        <ReturnButton onClick={()=>{setOpenRecipes(false)}}>X</ReturnButton>
      {recipes !== [] && recipes.map(recipe => <Recipe key={uuidv4()} recipe={recipe}/>)} 
    </AppContent>
      }
      
    </StyledApp>
  );
}

export default App;
