import React, {useState} from 'react'
import styled from 'styled-components'
import RecipeDetails from './RecipeDetails'

const StyledRecipe = styled.div`
width: 260px;
padding: 30px 20px 30px 20px;
display: flex;
flex-direction: column;
align-items: center;
margin: 15px;
position: relative;
background-color: #626f6e;
box-shadow: 1px 1px 1px 1px #656565;
height: fit-content;
border-radius: 5px;
`
const RecipeTitle = styled.div`
display: flex;
flex-wrap: wrap;
position: absolute;
top: 5;
z-index: 2;
background-color: #ffd102;
width: 220px;
justify-content: center;
align-items: center;
font-size: 15px;
height: fit-content;
opacity: 0.85;
color: white;
padding: 10px;
font-weight: 600;
font-size: 16px;
`

const RecipeImage = styled.div`
position: relative;

img {
    height: 240px;
}
`

const RecipeUrl = styled.div`
margin-top: 5px;
background-color: white;
width: 240px;
text-align: center;
a {
    text-decoration: none;
    color: #3d87a7;
    
    &:hover {
        color: #666666;
    }
}
`

const RecipeIngredients = styled.div`
background-color: #3d87a7;
color: white;
width: 232px;
text-align: center;
padding: 4px;
cursor: pointer;

&:hover {
    background-color: #ffd102;
}
`

const RecipeMore = styled.div`

`


const Recipe = ({recipe}) => {
    const [show, setShow] = useState(false)
    const {label, image, url, ingredients} = recipe.recipe;
    return (
        <StyledRecipe>
            <RecipeTitle>{label}</RecipeTitle>
            <RecipeImage><img src={image} alt={label} /></RecipeImage>
            
            <RecipeIngredients onClick={()=> setShow(!show)}>Ingredients</RecipeIngredients>
            <RecipeMore>
            {show &&
            <RecipeDetails ingredients={ingredients}/>}
            </RecipeMore>
            <RecipeUrl>
            <a href={url} target="_blank" rel="noopener 
            noreferrer">See recipe</a>
            </RecipeUrl>
        </StyledRecipe>
    )
}

export default Recipe
