import React from 'react'
import styled from 'styled-components'

const StyledAlert = styled.div`
background-color: orange;
width: 100%;
text-align: center;
position: absolute;
top: 0;
left: 0;

h3 {
    color: white;
}
`

const Alert = ({alert}) => {
    return (
        <StyledAlert>
           <h3> {alert}</h3>
        </StyledAlert>
    )
}

export default Alert
